$primary: #1890ff;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.input__field {
  background: transparent;
  border: none;
  border: 1px solid #e0e5ec;
  color: black;
  flex: 1;
  display: block;
  height: auto;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 5px;
  margin-top: 0 !important;
}

.input__field-group {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.form-group {
  label {
    font-weight: 500;
    margin-bottom: 5px;
  }

  font-size: 14px;

  input {
    font-size: 14px;
    padding: 8px 12px;
    height: 40px;
    border-radius: 3px;
  }
}

.error {
  position: absolute;
  // color: $secondary;
}

.text-danger {
  color: #a94442 !important;
}
