$primary: #1890ff;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.rounded-bottom-0 {
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

.form-control-lg {
  height: calc(1.5em + 2.25rem + 2px);
  padding: 1.125rem 1.125rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.3rem;
}

.form-control:focus {
  color: #151b26;
  background-color: #fff;
  border-color: #ced4da;
  outline: 0;
  box-shadow: 0 0 0 0 transparent;
}

.btn-search {
  padding: 0.75rem 1rem;
}

@media (min-width: 768px) {
  .rounded-md-right-0 {
    border-top-right-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
  }

  .h-md-100 {
    height: 100%;
  }

  .rounded-md-left-0 {
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
  }
}

@media (min-width: 992px) {
  .w-lg-80 {
    width: 80% !important;
  }
}
