$primary: #1890ff;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.review-cv {
  &--vertical {
    flex-direction: column-reverse;
  }

  &--layout {
    display: flex;
    margin: 0 20px;
    align-items: flex-start;

    .message-Xfb-4 {
      align-self: stretch;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      margin: 16px 0;

      .review-cv__icon {
        padding-left: 0;
        flex: 0 0 auto;

        img {
          width: 53px;
          height: unset;
        }
      }

      ul {
        flex: 1 1 auto;
        color: #4b4b4b;
        font-size: 1rem;
        padding-left: 32px;

        li:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
  }

  img {
    vertical-align: middle;
    border: 0;
  }
}

.rv-button {
  padding: 10px 1.5rem;
  color: #fff;
  box-sizing: border-box;
  font-size: 1rem;
  letter-spacing: 0;
  font-weight: 700;

  &:hover {
    background-color: #0044e3;
    border-color: #0044e3;
  }

  &--primary {
    margin: 0;
    text-align: center;
    background-color: #085ff7;
    border: 0.125rem solid #085ff7;
    background-repeat: repeat-x;
    border-radius: 6.25rem;
    cursor: pointer;
  }

  &--block {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 100%;
    max-width: 300px;
  }
}
